$visual_hover_color : #4136BF; 
$text_backgroundcolor: #FFBF00;


* {
    margin: 0;
    padding: 0;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

body{
    font-family: 'Lato', sans-serif;
    font-family: 'Noto Sans KR', sans-serif;

}

h2{
    font-family: 'Lato', sans-serif;
                font-family: 'Permanent Marker', cursive;
                font-weight: 400;
}


.swiper {
    .swiper-wrapper {

        .swiper-slide {
            background-color: #fff;
            height: 300px;

            p {
                top: 0;
                left: 0;
                position: absolute;
                display: inline-block;
                width: 45px;
                font-size: 17px;
                text-align: center;
                background-color: $text_backgroundcolor;
                font-weight: bold;
            }

            img {
                width: 100%;
                height: 100%;
            }

            .more {
                width: 100%;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.3);
                position: absolute;
                opacity: 0;
                transition: all 0.3s;

                &:hover {
                    opacity: 1;
                }

                p {
                    width: 100%;
                    font-size: 12px;
                    background-color: transparent;
                    font-weight: 100;
                    left: 50%;
                    transform: translateX(-50%);
                    top: 120px;
                    color: white;
                }

                .more_btn {
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                    bottom: 50px;
                    cursor: pointer;
                    border: none;
                    background-color: $text_backgroundcolor;
                    width: 105px;
                    height: 35px;
                    border-radius: 5px 5px;
                }
            }
        }

        margin-bottom: 10px;


        .swiper-pagination {
            bottom: var(--swiper-pagination-bottom, -4px);

            .swiper-pagination-bullet {
                margin: 0px var(--swiper-pagination-bullet-horizontal-gap, 95px);
            }
        }
    }
}

//swiper



.visual_main_outer {

    .visual_swiper {
        position: relative;
        width: 1245px;
        height: 383px;
        margin: 0 auto;

        .swiper-wrapper {
            width: 1245px;
            height: 350px;
            
            .swiper-slide {
                width: 100%; height: 100%;
                display: flex;
                flex-direction: column;
                background-color: whitesmoke;
                &:nth-child(1){
                    // background-image: url(../images/visual_06.jpg);
                    // background-size: cover;
                }

                .visual_main {
                    width: 1245px;
                    height: 350px;
                    overflow: hidden;
                    img{
                        width: 100%; height: 100%;
                    }
                }


                .visual_textbox {
                    position: absolute;
                    bottom: 85px;

                    .visual_text {
                    h2{
                        font-family: 'Lato', sans-serif;
                        font-family: 'Noto Sans KR', sans-serif;
                        font-weight: bold;
                        color: white;
                    }
                        p {
                            margin: 10px 0;
                            color: white;
                            font-weight: 100;
                        }

                    }

                    .visual_btn {
                        font-family: 'Lato', sans-serif;
                        font-family: 'Noto Sans KR', sans-serif;

                        // button {
                        //     border-radius: 5px;
                        //     border: none;
                        //     background-color: #FFBF00;
                        //     width: 129px;
                        //     height: 40px;
                        //     cursor: pointer;
                        // }

                        button{
                            border: none;
                            background-color: black;
                            color: white;
                            width: 120px;
                            height: 40px;
                            cursor: pointer;
                            transition: all 0.3s;
                            border-radius: 5px;
                            &:hover{
                                background-color: #FFBF00;
                                color: black;

                            }
                        }
                    }
                }
            }

            .swiper-button-prev,
            .swiper-button-next {
                opacity: 0.5;
                padding: 15px 5px;
                border-radius: 20px;
                color: black !important;
            }

            .swiper-button-prev:after,
            .swiper-button-next:after {
                font-size: 1.1rem !important;
                font-weight: 600 !important;
            }

            .swiper-pagination {
                bottom: -85px;
                .swiper-pagination-bullet{
                    margin: 0 150px;
                }
                .swiper-pagination-text{
                }

            }
        }//swiper-wrapper
    }

    //visual_swiper


    .swiper {
        width: 100%;
        height: 100%;

        .swiper-pagination-bullet {
            margin: 0 100px;
        }

    }

    .swiper-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .swiper-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .swiper-pagination-bullet {
        margin: 0 60px;
    }

}

