.md_pick {
    width: 1245px;
    height: 493px;
    margin: 60px auto;
    text-align: center;

    .md_pick_title {
        padding: 15px 0;
    }

    .md_pick_outer {
        display: flex;

        .md_content {
            position: relative;
            width: 200px;
            height: 400px;
            background-color: #afafaf;
            margin-right: 15px;
            transition: all 0.3s;

            &:nth-child(1) {
                width: 615px;
            }

            img {
                width: 100%;
                height: 100%;
            }

            .md_content_text {
                display: none;
                width: 50%;
                height: 100%;
                background-color: rgba(119, 119, 119, 0.3);
                position: absolute;
                top: 0;
                right: 0;
                color: white;


                p {
                    font-size: 16px;
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                    top: 90px;

                    span {
                        font-weight: bold;
                    }

                }

                .md_more_btn {
                    cursor: pointer;
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                    bottom: 60px;
                    background-color: $text_backgroundcolor;
                    width: 105px;
                    height: 35px;
                    font-size: 12px;
                    border: 0;
                    border-radius: 5px;

                }
            }

            .md_text_01 {
                display: block;
            }
        }

        //md_content
        .md_04 {
            margin-right: 0;
        }
    }
}

